import React, { useState } from 'react';

function Artists({ data, visible, windowSize, mobile }) {
  const [selected, setSelected] = useState(null);

  const handleClick = (item) => {
    setSelected(selected?.id === item.id ? null : item);
  };

  const onReturn = () => {
    setSelected(null);
  };

  const imageBgStyle = (img) => {
    return {
      backgroundImage: `url(${img})`,
      backgroundSize: 'cover',
      backgroundPosition: 'center',
    };
  };

  const ArtistCard = ({ item }) => {
    const current = selected?.id === item.id;
    return (
      <div className="grid-item">
        <div
          className="artist-card"
          onClick={() => handleClick(item)}
          style={{ borderColor: current ? '#000' : '#bbb' }}
        >
          <div className="artist-image" style={current ? null : imageBgStyle(item.image)}>
            {current && (
              <div style={{ padding: '1em' }}>
                https://art21.org/artist/adam-milner/
                <br />
                <br />
                https://blackcube.art/fellow/adam-milner
                <br />
                <br />
                https://blackcube.art/fellow/adam-milner
                <br />
                <br />
                https://blackcube.art/fellow/adam-milner
                <br />
                <br />
                https://blackcube.art/fellow/adam-milner
              </div>
            )}
          </div>
          <div
            style={
              current && !mobile
                ? { borderWidth: '1px 0 0 0', borderColor: '#000', borderStyle: 'solid' }
                : null
            }
          >
            <div className="artist-info">
              <div className="artist-name">{item.name}</div>
              <div className="artist-tags">
                {item.tags.map((tag, i) => (
                  <div key={`${tag}-${i}`} className="artist-tag">
                    {tag}
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className="grid-scrollview" style={!visible ? { display: 'none' } : null}>
      <div className="grid">
        {data.map((item) => (
          <ArtistCard key={item.id} item={item} />
        ))}
        {!data.length && (
          <div className="no-results">No artists match your criteria.</div>
        )}
        {/* <ArtistView
          artist={selected}
          onReturn={onReturn}
          windowSize={windowSize}
          mobile={mobile}
        /> */}
      </div>
    </div>
  );
}

export default Artists;

function ArtistView({ artist, onReturn, windowSize, mobile }) {
  if (!artist) {
    return null;
  }
  return (
    <div
      className="grid-item selected"
      style={{ width: mobile ? '100%' : windowSize.width - 248 }}
    >
      <div className="artist-card selected" onClick={onReturn}>
        <div>{artist.name}</div>
        <div>{artist.img}</div>
        <div>{artist.info}</div>
        <div>{artist.tags}</div>
      </div>
    </div>
  );
}
